.details-page{
  background-color: var(--bg-color);
  height: 100%;
}

.details-page .container{
  padding: 3em  5%;
}

.back-button{
  color: var(--text-color);
  background-color: var(--elements-color);
  box-shadow: var(--box-shadow);
  padding: 0.35em 2em;
  border-radius: var(--border-radius-default);
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 0.6em;
  margin-bottom: 5em;
}

.country{
  display: grid;
  grid-gap: 2.5em;
  grid-template:
  "flag" max-content
  "details" max-content
  / 100%;
  ;
}

.header{
  margin-right: 0.35em;
}

.details-flag{
  display: block;
  max-width: 30em;
  grid-area: flag;
}

.details-info{
  grid-area: details;
}

.details-flag img{
  display: block;
}

.country-name{
  font-weight: var(--fw-extrabold);
  font-size: 1.7em;
  margin-bottom: 0.5em;
}

.country-detail .header{
  font-weight: var(--fw-semibold);
}

.country-details{
  display: flex;
  flex-direction: column;
  gap: 2.5em;
  font-size: 0.85rem;
}

.country-detail + .country-detail{
  margin-top: 0.4em;
}

.bottom .header{
  font-size: 1.2rem;
}

/* .country-lang + .country-lang{
  margin-left: 0.35em;
} */

.border-countries{
  display: flex;
  flex-flow: row wrap;
  gap: 0.5em;
}

.country-tag{
  padding: 0.35em 1.7em;
  color: var(--text-color);
  background-color: var(--elements-color);
  box-shadow: var(--box-shadow);
}

@media(min-width: 1000px){
  .country{
    grid-gap: 5%;
    grid-template:
    "flag details" max-content
    / minmax(100px,50em) minmax(100px,50em);
    ;
    justify-content: center;
  }

  .details-flag{
    max-width:none;
    display: block;
    align-self: center;
  }

  .details-info{
    align-self: center;
  }

  .country-details{
    padding-right: 4em;
    flex-flow: row wrap;
    gap: 2.5em minmax(5%,4em);
    justify-content: space-between;
    font-size: 1rem;
  }

  section .top{
    width: min-content;
  }

  section .middle{
    width: min-content;
  }

}